.l-grid {
  display: grid;
  align-items: center;
  &--col2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  &--col2-auto {
    display: grid;
    grid-template-columns: repeat(2, auto);
  }
  &--col3 {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
  }
  &--col3-auto {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(3, auto);
  }
  &--col4 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  &--col4-auto {
    display: grid;
    grid-template-columns: repeat(4, auto);
  }
  &--col5 {
    display: grid;
    grid-template-columns: repeat(5, auto);
  }
  &--start {
    align-items: start;
  }
  &--end {
    align-items: end;
  }

  &__right-submitButton {
    display: grid;
    grid-template-columns: 1fr 160px;
    gap: 3.5rem;
    align-items: end;
    &--button {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      font-size: 1rem;
    }
  }

  // ä»£ç†åº—è¨­å®šãƒ»ä»£ç†åº—æ–™é‡‘è¡¨è¨­å®š
  &__agency {
    label {
      font-size: 0.875rem;
    }

    &--up {
      input {
        width: 100%;
      }
    }

    &--center {
      display: grid;
      grid-template-columns: 120px 120px 120px 1fr;
      align-items: center;
      margin-bottom: 1rem;
    }
    
    &--bottom {
      display: grid;
      grid-template-columns: 1fr 160px 160px;
      gap: 2rem;
      input {
        width: 100%;
      }
    }
  }

  // åŸºæœ¬æ–™é‡‘è¨­å®š(basic_pricing)
  &__basicPricing {
    &--bottom {
      input {
        width: 150px;
      }

      &--left {
        display: grid;
        column-gap: 1rem;
        align-items: center;
        grid-template-columns: auto 1fr;
      }
      &--right {
        text-align: right;
        display: grid;
        column-gap: 1rem;
        align-items: center;
        grid-template-columns: 1fr 150px;
      }
    }
  }
}

// gap
.l-grid {
  &--gap025 {
    gap: 0.25rem;
  }
  &--gap05 {
    gap: 0.5rem;
  }
  &--gap1 {
    gap: 1rem;
  }
  &--gap2 {
    gap: 2rem;
  }
  &--cgap025 {
    gap: .25rem;
  }
  &--cgap05 {
    gap: .5rem;
  }
  &--cgap1 {
    column-gap: 1rem;
  }
  &--cgap2 {
    column-gap: 2rem;
  }
  &--cgap2-half {
    column-gap: 2.5rem;
  }
  &--rgap1 {
    row-gap: 1rem;
  }
  &--rgap2 {
    row-gap: 2rem;
  }
}

// column 
.l-grid {
  &--rowspan1 {
    grid-row: span 1;
  }
  &--rowspan2 {
    grid-row: span 2;
  }
  &--rowspan3 {
    grid-row: span 3;
  }
  &--colspan1 {
    grid-column: span 1;
  }
  &--colspan2 {
    grid-column: span 2;
  }
  &--colspan3 {
    grid-column: span 3;
  }
  &--colspan4 {
    grid-column: span 4;
  }
}

// input ã¨ label ã®grid
.l-grid--input {
  > * {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  align-items: center;
  }
}

.l-grid__radio {
  display: grid;
  grid-template-columns: auto auto;
  gap: 0.25rem;
  align-items: center;
  label {
    white-space: nowrap;
  }
}

.l-grid-input {
  display: grid;
  gap: var(--gap-s);
  grid-template-columns: repeat(auto-fit, minmax(200px, auto));
